import { Component, OnInit} from '@angular/core';
import { AbstractControl,FormControl,FormArray,FormBuilder, FormGroup, Validators,ValidatorFn} from '@angular/forms';
import {ValidatService} from '../../services/validat.service';
import{AppComponent} from '../../app.component'

function passwordMach(c: AbstractControl  ){
  return c.get('password').value === c.get('confirm').value ? null : { 'notMatch' : true};
}


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
 
  form: FormGroup;

  constructor(public fb: FormBuilder) {
 
    
  }

  ngOnInit() {
    this.form = this.fb.group({
      email       :['',[Validators.required, ValidatService.emailValidator]],
      passG  :this.fb.group({
        password    :['',[Validators.required,ValidatService.passwordValidator]],
        confirm     :''
      },{validator : passwordMach})
    });
  }

registerOnSubmit(){

  console.log(this.form.value)
}

}



